* {font-family: "Lato", sans-serif;}

$font-weight-bold: 600;

$dark: #282828;
$light: #F2F2F2;
$primary: #FFDD00;
$secondary: #002244;

.container {
    max-width: 96%;
    @include sm{width: 96%;}
    @include md{width: 96%;}
    @include lg{width: 96%;}
    @include xl{max-width: 1360px;}
}

p{
    font-size: 0.8575em !important;
    line-height: 1.4rem;
    @include md{font-size: 1em !important;}
}

.h1, h1{
    font-size: 2.4em !important;
    @include sm{font-size: 2.6em !important;}
    @include md{font-size: 2.8em !important;}
    @include lg{font-size: 3.2em !important;}
}

.h2, h2{
    font-size: 2.2em !important;
    @include sm{font-size: 2.2em !important;}
    @include md{font-size: 2.4em !important;}
    @include lg{font-size: 2.6em !important;}
}

.h3, h3{
    font-size: 2em !important;
    @include sm{font-size: 2.1em !important;}
    @include md{font-size: 2.2em !important;}
    @include lg{font-size: 2.4em !important;} 
}

.h4, h4{
    font-size: 1.6em !important;
    @include sm{font-size: 1.8em !important;}
    @include md{font-size: 2em !important;}
    @include lg{font-size: 2.2em !important;} 
}

.h5, h5{
    font-size: 1.2em !important;
    @include sm{font-size: 1.4em !important;}
    @include md{font-size: 1.6em !important;}
    @include lg{font-size: 1.8em !important;} 
}

.h6, h6{
    font-size: 1.1em !important;
    @include sm{font-size: 1.15em !important;}
    @include md{font-size: 1.2em !important;}
    @include lg{font-size: 1.25em !important;} 
}
